import VueRouter from 'vue-router';
// import Home from '../views/Home.vue'
import Intro from '../views/Intro.vue'
import Execute from '../views/Execute.vue'
import About from '../views/About.vue'
import Algorithm from '../views/Algorithm.vue'

//创建一个路由器
const router = new VueRouter({
    mode: 'history',
    routes:[
        {
            name: 'home',
            path:'/',
            component:Intro,
        },
        {
            name: 'home',
            path:'/home',
            component:Intro,
        },
        {
            name: 'execute',
            path:'/execute',
            component:Execute,
        },
        {
            name:'about',
            path:'/about',
            component:About,
        },
        {
            name:'algorithm',
            path:'/algorithm',
            component:Algorithm,
        },

    ]
})
export default router

  
