import Vue from 'vue'
import App from './App.vue'
//引入路由
import VueRouter from 'vue-router';
import router from './router/index.js';
import axios from 'axios';

Vue.prototype.$axios = axios
Vue.config.productionTip = false
Vue.use(VueRouter)

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
